/* colors:
#e9008d pink
#f6841b orange
#fcea08 yellow
#6eb63f green
#05aded blue
#A032A6 purple
*/


.App {
  text-align: center;
  background-color: #F7ADCE;
}

html, body {
  height: 100%
}

.header {
  margin-bottom: 2em;
}

.card border-light card mb-4{
  background-color:#F7ADCE;
}

.characterHeader {
  text-align: center;
}

#characterText {
margin-top: -2em;
}

#characterImage {
  margin-top: 1.1em;
  margin-bottom: 1.1em;
}

.rainbow-text {
  font-family: 'Pacifico', cursive;
  text-align: center;
  background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);   
  background-clip: text;
  color: transparent;
}

p {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.2em;
  text-align: justify;
  font-weight: 800;
}




